import React, { Component } from 'react'
class Widecard extends Component {
render() {
return (
<div class="widecard">
<div class="compdet">
<h3>{this.props.title}</h3>
<h4 class="secondtext">{this.props.blurb}</h4>
<h5 class="secondtext">{this.props.live}</h5>
</div>
</div>
)
}
}
export default Widecard

