import React, { Component } from 'react'
class Skills extends Component {
constructor(props)
{
super(props);
this.state={
'myskills':['HTML','CSS','Javascript','SQL','Data','ReactJS','Tableau']
};
}
render() {
return (
<div className="skills">
{this.state.myskills.map((value)=>{
return <div className="skillList">{value}</div>
})}
</div>
)
}
}
export default Skills