import React, { Component } from 'react';
import Widecard from '../components/Widecard';
class Projects extends Component {
render() {
return (
<div className="projects">
<h1 className="subtopic">My Projects</h1>
<Widecard 
title="React To-Do List" 
blurb="A simple, fully functioning to-do list. Makes good use of React concepts." 
live={<a href="https://joeymonachino.github.io/todolist/" target="_blank">View Live</a>} 
/*github={<a href="https://github.com/joeymonachino/todolist" target="_blank">View Code</a>}*/
/>
<Widecard 
title="Video Editing" 
blurb="I compiled some of my gaming highlights and made it into a montage. I am still working on getting better at this." 
live={<a href="https://youtu.be/enWYmYO0GL0" target="_blank">View Video</a>}  
/>
<Widecard 
title="Tableau Public" 
blurb="I have a few from-scratch Tableau Visualizations of Esports data." 
live={<a href="https://public.tableau.com/app/profile/joseph.monachino#!/" target="_blank">View Tableau Public</a>}  
/>
</div>
)
}
}
export default Projects


